import { FunctionComponent } from 'preact';
import { useState, useEffect } from 'preact/hooks';
import { UIProvider } from '@adobe/elsie/components';
import { Lang } from '@adobe/elsie/i18n';
import { events } from '@adobe/event-bus';
import { config } from '@/order-confirmation/api';
import { deepmerge } from '@adobe/elsie/lib';

import en_US from '../i18n/en_US.json';

// Langs
const langDefinitions = {
  default: en_US,
};

interface ProviderProps {
  children?: any;
}

export const Provider: FunctionComponent<ProviderProps> = ({ children }) => {
  const [locale, setLang] = useState<Lang>();

  const userLangDefinitions = config.getConfig()?.langDefinitions;

  //   Events
  useEffect(() => {
    const localeEvent = events.on(
      'locale',
      (payload) => {
        if (payload !== locale) setLang(payload as Lang);
      },
      { eager: true }
    );
    return () => {
      localeEvent?.off();
    };
  }, [locale]);

  // Merge language definitions with user language definitions
  const definitions = deepmerge(langDefinitions, userLangDefinitions ?? {});

  return (
    <UIProvider lang={locale} langDefinitions={definitions}>
      {children}
    </UIProvider>
  );
};
